import Vue from 'vue'
import Router from 'vue-router'

// Containers
import Full from '@/containers/Full'

// Views
import Assets from '@/views/Assets'
import Transfer from '@/views/Transfer'
import History from '@/views/History'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/assets',
      name: 'Bench',
      component: Full,
      children: [
        {
          path: 'assets',
          name: 'Assets',
          component: Assets
        },
        {
          path: 'transfer',
          name: 'Transfer',
          component: Transfer
        },
        {
          path: 'history',
          name: 'History',
          component: History
        }
      ]
    }
  ]
})



