<template>
  <div class="animated fadeIn">
    <div class="card">
      <div class="card-body">
        <b-table hover striped responsive="sm" :items="items" :fields="fields" >
          <template slot="operation" slot-scope="data">
            <b-button variant="primary">Transfer</b-button>
          </template>
        </b-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'assets',
  data () {
    return {
      items: [
        {
          id: '1.3.0',
          symbol: 'BTS',
          amount: '10000',
          operation: 'transfer'
        },
        {
          id: '1.3.0',
          symbol: 'BTS',
          amount: '10000',
          operation: 'transfer'
        },
        {
          id: '1.3.0',
          symbol: 'BTS',
          amount: '10000',
          operation: 'transfer'
        },
        {
          id: '1.3.0',
          symbol: 'BTS',
          amount: '10000',
          operation: 'transfer'
        },
        {
          id: '1.3.0',
          symbol: 'BTS',
          amount: '10000',
          operation: 'transfer'
        }
      ],
      fields: [
        {key: 'symbol'},
        {key: 'amount'},
        {key: 'operation'}
      ]
    }
  }
}
</script>



